export function useReport() {

  const initialReport = (organizationId, memberUuid, weekString) => {
    const initialRatings = ratingItems.reduce((acc, item) => {
      acc[item.key] = 0
      return acc
    }, {})

    return {
      organizationId,
      memberUuid,
      weekString,
      projects: [{ name: '', workItems: [{ content: '' }] }],
      overtimeHours: 0,
      issues: '',
      improvements: '',
      rating: initialRatings
    }
  }

  const ratingItems = [
    {
      key: 'stress',
      label: '',
      itemLabels: ['', '', '', '', ''],
      negative: true
    },
    {
      key: 'disability',
      label: '',
      itemLabels: ['', '', '', '', ''],
      negative: true
    },
    {
      key: 'achievement',
      label: '',
      itemLabels: ['', '', '', '', ''],
      negative: false
    }
  ]

  const statusOptions = [
    { value: 'all', text: '', color: 'indigo' },
    { value: 'none', text: '', color: 'error' },
    { value: 'pending', text: '', color: 'success' },
    { value: 'feedback', text: '', color: 'warning' },
    { value: 'approved', text: '', color: 'primary' }
  ]

  const getStatusText = (status) => {
    const option = statusOptions.find((e) => e.value === status)
    return option ? option.text : ''
  }

  const getStatusColor = (status) => {
    const option = statusOptions.find((e) => e.value === status)
    return option ? option.color : ''
  }

  return {
    initialReport,
    ratingItems,
    statusOptions,
    getStatusText,
    getStatusColor
  }
}  