<template>
  <v-container>
    <v-card max-width="500" class="mx-auto mt-6 px-4 rounded-lg">
      <v-card-title class="mt-1 mb-4">
        <div class="text-center mb-5">
          <router-link to="/" class="text-indigo-darken-2 text-decoration-none">
            <h1 class="logo-font">fluxweek</h1>
          </router-link>
        </div>
        <v-icon size="large" class="mr-1">mdi-bird</v-icon>
        {{ title }}
      </v-card-title>
      <v-card-text>
        <v-alert
          v-if="successMessage"
          type="success"
          class="resultMessage mb-4"
          dismissible
          @click:close="successMessage = ''"
        >
          {{ successMessage }}
        </v-alert>        
        <v-alert 
          v-if="errorMessage" 
          type="error" 
          class="resultMessage mb-4"
          dismissible
          @click:close="errorMessage = ''"
        >
          {{ errorMessage }}
          <div v-if="isAlreadyAuthenticated">
            <v-btn
              color="white"
              variant="tonal"
              class="mt-2"
              @click="handleSignOut"
            >
              
            </v-btn>
          </div>
        </v-alert>

        <!--  -->
        <v-form v-if="currentView === 'signIn'" @submit.prevent="handleSignIn">
          <v-text-field
            v-model="signInEmail"
            label=""
            required
          />
          <v-text-field
            v-model="signInPassword"
            label=""
            type="password"
            required
          />
          <v-btn
            color="primary"
            type="submit"
            block
            class="mt-4"
            :loading="loading"
          >
            
          </v-btn>
        </v-form>

        <!--  -->
        <v-form v-else-if="currentView === 'signUp'" ref="signUpForm" @submit.prevent="handleSignUpSubmit">
          <v-text-field
            v-model="signUpEmail"
            label=""
            type="email"
            required
            :rules="[v => !!v || '', v => /.+@.+\..+/.test(v) || '']"
          />
          <v-text-field
            v-model="signUpPassword"
            label=""
            type="password"
            required
            :rules="[v => !!v || '', v => v.length >= 8 || '8']"
          />
          <v-text-field
            v-model="organizationId"
            label="ID"
            required
            :rules="[validateOrganizationId]"
            @input="clearErrorMessage"
          />
          <v-text-field
            v-model="organizationName"
            label=""
            required
            :rules="[v => !!v || '']"
          />
          <v-checkbox
            v-model="agreeToTerms"
            label=""
            required
            hide-details
          >
            <template #label>
              <span>
                <a href="#" @click.prevent="openTermsOfService"></a>
              </span>
            </template>
          </v-checkbox>
          <v-checkbox
            v-model="agreeToPrivacy"
            label=""
            required
            hide-details
          >
            <template #label>
              <span>
                <a href="#" @click.prevent="openPrivacyPolicy"></a>
              </span>
            </template>
          </v-checkbox>
          <v-btn
            color="primary"
            type="submit"
            block
            class="mt-4"
            :loading="loading"
            :disabled="loading || !isAgreedToAll"
          >
            
          </v-btn>
        </v-form>

        <!--  -->
        <v-form v-else-if="currentView === 'confirm'" @submit.prevent="confirmSignUpUser">
          <v-text-field
            v-model="confirmEmail"
            label=""
            required
          />
          <v-text-field
            v-model="confirmCode"
            label=""
            required
          />
          <v-btn
            color="primary"
            type="submit"
            block
            class="mt-4"
            :loading="loading"
          >
            
          </v-btn>
          <v-btn
            text
            color="secondary"
            :disabled="loading"
            class="mt-2"
            @click="resendConfirmationCode"
          >
            
          </v-btn>
        </v-form>

        <!--  -->
        <v-form v-else-if="currentView === 'changePassword'" @submit.prevent="handleChangePassword">
          <v-text-field
            v-model="newPassword"
            label=""
            type="password"
            required
            :rules="[
              v => !!v || '',
              v => v.length >= 8 || '8'
            ]"
          />
          <v-text-field
            v-model="confirmNewPassword"
            label=""
            type="password"
            required
            :rules="[
              v => !!v || '',
              v => v === newPassword || ''
            ]"
          />
          <v-btn
            color="primary"
            type="submit"
            block
            class="mt-4"
            :loading="loading"
          >
            
          </v-btn>
        </v-form>

        <v-card-actions>
          <v-spacer />
          <v-btn
            text
            color="primary"
            @click="toggleView"
          >
            {{ toggleButtonText }}
          </v-btn>
        </v-card-actions>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script setup>
import { ref, computed } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { signIn, signUp, confirmSignUp, resendSignUpCode, confirmSignIn, signOut } from '@aws-amplify/auth'
import { termsOfServiceUrl, privacyPolicyUrl, termsOfServiceVersion, privacyPolicyVersion } from '@/config/environment'
import { getOrganization, submitOrganization } from '@/services/publicService'
import { validateOrganizationId } from '@/utils/string-utils'

const router = useRouter()
const store = useStore()
const route = useRoute()

// State
const currentView = ref(route.path === '/signup' ? 'signUp' : 'signIn')
const signInEmail = ref('')
const signInPassword = ref('')
const signUpEmail = ref('')
const signUpPassword = ref('')
const organizationId = ref('')
const organizationName = ref('')
const confirmEmail = ref('')
const confirmCode = ref('')
const newPassword = ref('')
const confirmNewPassword = ref('')
const loading = ref(false)
const successMessage = ref('')
const errorMessage = ref('')
const signUpForm = ref(null)
const agreeToTerms = ref(false)
const agreeToPrivacy = ref(false)
const isAlreadyAuthenticated = ref(false)

// Computed properties
const title = computed(() => {
  const titles = {
    signIn: '',
    signUp: '',
    confirm: '',
    changePassword: ''
  }
  return titles[currentView.value] || ''
})

const toggleButtonText = computed(() => {
  const texts = {
    signIn: '',
    signUp: '',
    confirm: '',
    changePassword: ''
  }
  return texts[currentView.value] || ''
})

const isAgreedToAll = computed(() => agreeToTerms.value && agreeToPrivacy.value)

// Methods

const clearErrorMessage = () => {
  errorMessage.value = ''
}

const handleSignIn = async () => {
  loading.value = true
  errorMessage.value = ''
  successMessage.value = ''
  try {
    const signInResult = await signIn({ 
      username: signInEmail.value, 
      password: signInPassword.value 
    })

    // 
    if (signInResult.nextStep?.signInStep === 'CONFIRM_SIGN_IN_WITH_NEW_PASSWORD_REQUIRED') {
      currentView.value = 'changePassword'
      // 
      sessionStorage.setItem('requiresNewPassword', 'true')
      sessionStorage.setItem('tempUsername', signInEmail.value)
      return
    }

    // 
    try {
      await store.dispatch('auth/fetchUser')
      await router.push('/admin')
    } catch (fetchError) {
      console.error('User fetch error:', fetchError)
      if (fetchError.message?.includes('NEW_PASSWORD_REQUIRED')) {
        currentView.value = 'changePassword'
        return
      }
      throw fetchError
    }

  } catch (error) {
    console.error('SignIn error:', error)
    handleAuthError(error)
  } finally {
    loading.value = false
  }
}

const handleSignUpSubmit = async () => {
  errorMessage.value = ''
  const { valid } = await signUpForm.value.validate()
  
  if (!valid) {
    errorMessage.value = ''
    return
  }

  if (!validateOrganizationId(organizationId.value)) {
    errorMessage.value = 'ID'
    return
  }

  const org = await getOrganization(organizationId.value)
  if (org) {
    errorMessage.value = 'ID'
    return
  }

  await signUpUser()
}

const signUpUser = async () => {
  loading.value = true
  try {
    const { user } = await signUp({
      username: signUpEmail.value,
      password: signUpPassword.value,
      options: {
        userAttributes: {
          email: signUpEmail.value,
          name: signUpEmail.value,
          'custom:organizationId': organizationId.value,
          'custom:tos_version': termsOfServiceVersion,
          'custom:pp_version': privacyPolicyVersion
        }
      }
    })
    console.log(':', user)
    confirmEmail.value = signUpEmail.value
    currentView.value = 'confirm'
    successMessage.value = ''
  } catch (error) {
    console.error(':', error)
    errorMessage.value = error.message || ''
  } finally {
    loading.value = false
  }
}

const confirmSignUpUser = async () => {
  loading.value = true
  errorMessage.value = ''
  try {
    await confirmSignUp({ username: confirmEmail.value, confirmationCode: confirmCode.value })

    // 
    const organization = { 
      organizationId: organizationId.value, 
      name: organizationName.value, 
      sender: 'notify@fluxweek.com',
      senderName: organizationName.value 
    }
    await submitOrganization(organization)
    currentView.value = 'signIn'
    successMessage.value = ''
  } catch (error) {
    console.error(':', error)
    errorMessage.value = ''
  } finally {
    loading.value = false
  }
}

const resendConfirmationCode = async () => {
  loading.value = true
  errorMessage.value = ''
  try {
    await resendSignUpCode({ username: confirmEmail.value })
    successMessage.value = ''
  } catch (error) {
    console.error(':', error)
    errorMessage.value = ''
  } finally {
    loading.value = false
  }
}

const handleChangePassword = async () => {
  if (newPassword.value !== confirmNewPassword.value) {
    errorMessage.value = ''
    return
  }

  loading.value = true
  errorMessage.value = ''

  try {
    if (sessionStorage.getItem('requiresNewPassword') === 'true') {
      // 
      const username = sessionStorage.getItem('tempUsername')
      await confirmSignIn({
        challengeResponse: newPassword.value,
        options: {
          userAttributes: {
            name: username // Add name attribute
          }
        }
      })
      await store.dispatch('auth/fetchUser')
      await router.push('/admin')
    } else {
      // 
      await store.dispatch('auth/updatePassword', {
        oldPassword: signInPassword.value,
        newPassword: newPassword.value
      })
    }

    sessionStorage.removeItem('requiresNewPassword')
    sessionStorage.removeItem('tempUsername')
    successMessage.value = ''
    currentView.value = 'signIn'
    newPassword.value = ''
    confirmNewPassword.value = ''
    signInPassword.value = ''
  } catch (error) {
    console.error('Password change error:', error)
    errorMessage.value = error.message || ''
  } finally {
    loading.value = false
  }
}

const handleSignOut = async () => {
  loading.value = true
  try {
    await signOut()
    errorMessage.value = ''
    isAlreadyAuthenticated.value = false
    successMessage.value = ''
  } catch (error) {
    console.error('Signout error:', error)
    errorMessage.value = ''
  } finally {
    loading.value = false
  }
}

const handleAuthError = (error) => {
  console.log('Auth error type:', error.name) // 
  console.log('Auth error message:', error.message) // 

  const errorMessages = {
    UserAlreadyAuthenticatedException: '',
    UserNotConfirmedException: '',
    NotAuthorizedException: '',
    UserNotFoundException: '',
    NewPasswordRequiredException: '',
    PasswordResetRequiredException: ''
  }

  // 
  if (error.name === 'UserNotConfirmedException') {
    confirmEmail.value = signInEmail.value
    currentView.value = 'confirm'
    errorMessage.value = errorMessages[error.name]
    return
  }

  // 
  if (error.name === 'NewPasswordRequiredException' || 
      error.message?.includes('NEW_PASSWORD_REQUIRED')) {
    currentView.value = 'changePassword'
    return
  }

  if (error.name === 'UserAlreadyAuthenticatedException') {
    isAlreadyAuthenticated.value = true
  }

  // 
  errorMessage.value = errorMessages[error.name] || 
    error.message || 
    ''
}

const toggleView = () => {
  currentView.value = currentView.value === 'signIn' ? 'signUp' : 'signIn'
  errorMessage.value = ''
}

const openTermsOfService = () => {
  window.open(termsOfServiceUrl, '_blank', 'noopener,noreferrer')
}

const openPrivacyPolicy = () => {
  window.open(privacyPolicyUrl, '_blank', 'noopener,noreferrer')
}
</script>

<style>
.resultMessage {
  white-space: pre-line;
}

.logo-font {
  color: inherit;
}
</style>